.resume-container {
  display: flex;
  align-items: center;
  margin-top: 1rem;
}

.left-container {
  width: 25%;
  padding: 1rem;
  background-color: var(--title-color);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.right-container {
  width: 75%;
  padding: 5rem;
}

.resume-hero {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  box-shadow: var(--shadow);
}

.resume-name {
  font-size: var(--h2-font-size);
  color: var(--body-color);
  margin-top: 1rem;
}

.resume-title {
  color: var(--body-color);
  opacity: 0.7;
}

.divider {
  width: 90%;
  height: 2px;
  background-color: var(--body-color);
  opacity: 0.2;
  margin-top: 1rem;
}

.contact-title,
.education-title {
  font-size: 1rem;
  color: var(--body-color);
  margin: 1rem 0;
  opacity: 0.7;
  text-transform: uppercase;
}

.contact-items {
  display: flex;
  flex-direction: column;
  align-items: start;
}

.contact-item {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.5rem;
}

.contact-item svg {
  color: var(--body-color);
  margin-right: 1rem;
}

.contact-item-description {
  color: var(--body-color);
}

.account-link {
  color: var(--body-color);
}

.education-items {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.education-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 0.5rem;
}

.education-time {
  color: var(--container-color);
  font-size: var(--small-font-size);
  text-align: center;
}

.education-name {
  color: var(--body-color);
  text-align: center;
  margin: 0.5rem 0 0.2rem;
}

.school-name {
  color: var(--container-color);
  opacity: 0.75;
  text-align: center;
}

.summary-title {
  font-size: 2rem;
}

.summary-items {
  list-style-type: square;
}

.summary-item {
  margin: 1rem 0;
}

@media (max-width: 576px) {
  .resume-container {
    flex-direction: column;
  }

  .left-container,
  .right-container {
    width: 100%;
  }
}
